@import "common/variables.scss";

.hero {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 302px;
    background-image: url("../img/cat-background.png");
    background-repeat: no-repeat;
    background-position: top;
    background-color: #63a73a;
    z-index: -1;

    @media (min-width: $tablet-width) {
      content: none;
    }

    @media (min-width: $desktop-width) {
      content: "";
      position: absolute;
      top: -98px;
      width: 50%;
      height: 100%;
      background-image: url("../img/cat-background-desktop.jpg");
      background-repeat: no-repeat;
      background-position: bottom right;
      background-size: contain;
    }
  }
}

.hero__wrapper {
  padding: 28px 0 0 0;

  @media (min-width: $tablet-width) {
    padding: 65px 0 0 90px;
  }

  @media (min-width: $desktop-width) {
    min-height: 694px;
    padding: 110px 0 20px 80px;
  }
}

.hero__title {
  margin: 0 0 25px 0;
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    margin: 0 0 40px 0;
    max-width: 425px;
    text-align: initial;
    color: #000000;
    font-size: 60px;
    line-height: 60px;
  }
}

.hero__description {
  margin: 0 0 29px 0;
  color: #ffffff;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    max-width: 425px;
    text-align: initial;
    color: #000000;
    font-size: 20px;
    line-height: 20px;
  }
}

.hero__link {
  color: #ffffff;
  text-align: center;

  @media (min-width: $tablet-width) {
    max-width: 244px;
  }
}
